.link-button {
    border: 1px solid black;
    border-radius: 15px;
    -webkit-transition: .5s;
    transition: .5s;
    width: 300px;
    height: 70px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 15px;
    text-decoration: none;
    outline: none;
}

.link-button:focus {
    box-shadow: 0 0 0 2pt grey;
}

.link-button:hover {
    background-color: var(--black);
    --color-button-text: #ffffff;
}

.icon{
    display: flex;
    justify-content: center;
    align-items: center;
}

@-webkit-keyframes circleLoadingAnimation {
    from {
        -webkit-transform: rotate3d(0, 0, 1, -180deg);
                transform: rotate3d(0, 0, 1, -180deg);
    }

    to {
        -webkit-transform: rotate3d(0, 0, 1, 180deg);
                transform: rotate3d(0, 0, 1, 180deg);
    }
}
@keyframes circleLoadingAnimation {
    from {
        -webkit-transform: rotate3d(0, 0, 1, -180deg);
                transform: rotate3d(0, 0, 1, -180deg);
    }

    to {
        -webkit-transform: rotate3d(0, 0, 1, 180deg);
                transform: rotate3d(0, 0, 1, 180deg);
    }
}
.loader {
    height: 150px;
    width: 150px;
    -webkit-animation: circleLoadingAnimation 2s linear infinite;
            animation: circleLoadingAnimation 2s linear infinite;
}
.open-sans-text {
    font-size: var(--font-size-main);
    font-family: var(--font-family-main);
    color: var(--black);
    text-align: center;
    -webkit-transition: .5s;
    transition: .5s;
}

.open-sans-text--button {
    color: var(--color-button-text);
}
.button {
    border: 1px solid black;
    border-radius: 15px;
    -webkit-transition: .5s;
    transition: .5s;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 300px;
    min-height: 70px;
    -webkit-transition: .2s;
    transition: .2s;
    outline: none;
}

.button:focus {
    box-shadow: 0 0 0 2pt grey;
}

.button:hover {
    background-color: var(--black);
    --color-button-text: #ffffff;
}
.error-modal {
    display: flex;
    flex-direction: column;
    border: 1px solid grey;
    border-radius: 20px;
    padding-top: 25px;
    width: 700px;
    min-height: 500px;
}

.error-modal__content {
    padding-top: 50px;
    min-height: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.error-modal__button {
    padding: 100px 0 25px 0;
    display: flex;
    justify-content: center;
}

@media screen and (max-width: 1199px) and (min-width: 768px) {
    .error-modal {
        min-height: 500px;
        width: 70%;
    }
    .error-modal__content {
        min-height: 100px;
    }
}

@media screen and (max-width: 767px) {
    .error-modal {
        min-height: 500px;
        width: 90%;
    }
    .error-modal__content {
        padding: 50px 10px 0 10px;
    }
}
.success-modal {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    border: 1px solid grey;
    width: 900px;
    min-height: 150px;
    padding: 20px 0;
    border-radius: 15px;
}

.success-modal__text {
    display: flex;
    justify-content: center;
}

.success-modal__button {
    padding-top: 20px;
}

@media screen and (max-width: 1199px) and (min-width: 768px) {
    .success-modal {
        width: 80%;
    }
}

@media screen and (max-width: 767px) {
    .success-modal {
        width: 90%;
    }
}
.user-form-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 20px;
    border: 1px solid grey;
    width: 85vw;
    max-width: 750px;
    min-height: 600px;
}

.user-form-wrapper--loading {
    display: flex;
    justify-content: center;
    align-items: center;
}

.user-form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.user-form__row {
    width: 80%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 30px 40px;
}

.user-form__row--button {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.user-form__field-block {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.user-form__text-field {
    font-size: var(--font-size-error);
    color: var(--black);
    padding: 0 10px;
    width: 250px;
    height: 50px;
    border-radius: 10px;
    border: 1px solid grey;
    outline: none;
    -webkit-transition: .2s;
    transition: .2s;
}

.user-form__text-field:focus {
    box-shadow: 0 0 0 2pt grey;
}

.user-form__error {
    height: 30px;
    display: flex;
}

@media screen and (max-width: 767px) {
    .user-form-wrapper {
        width: 95vw;
        max-height: 95vh;
        max-width: 460px;
    }

    .user-form__row:first-child {
        padding: 20px 40px 10px 40px;
        height: 63px;
    }

    .user-form__row {
        padding: 20px 20px;
    }

    .user-form__field-block {
        padding-top: 10px;
        flex-direction: column;
    }

    .user-form__error {
        height: 30px;
        justify-content: center;
    }

    .user-form__text-field {
        order: 1;
    }

    .user-form__label {
        order: 2;
        display: flex;
        justify-content: center;
    }
}
.error-message-custom {
    text-align: center;
    font-size: var(--font-size-error);
    color: var(--color-error-text)
}
.main-wrapper {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}
.send-page {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.verification-page {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.global-wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  height: 100vh;
  box-sizing: border-box;
  overflow: hidden;
}
html {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
}

body {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin: 0;
    padding: 0;
}

#root {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

:root {
    --font-family-main: 'OpenSans', 'Arial', 'sans-serif';
    --font-size-main: 30px;
    --color-button-text: #000000;
    --black: #000000;
    --color-error-text: #FF0000;
    --font-size-error: 25px;
}
@font-face {
    font-family: 'OpenSans';
    src: url(/static/media/OpenSans-Regular.a8874879.ttf);
    font-weight: 500;
}

@font-face {
    font-family: 'OpenSans-Bold';
    src: url(/static/media/OpenSans-Bold.07986db8.ttf);
    font-weight: 500;
}

@font-face {
    font-family: 'OpenSans-Light';
    src: url(/static/media/OpenSans-Light.3c5786e0.ttf);
    font-weight: 500;
}
